import React, { useState, useEffect } from "react"

import { FormControlLabel, FormControl, Radio, RadioGroup } from "@mui/material"
import ArrowUpwardIcon from '@mui/icons-material/ArrowUpward'
import ArrowDownwardIcon from '@mui/icons-material/ArrowDownward';

import { plot_color_palette } from "../../../utils/color"

import DistributionPlot from "../../../components/Plot/Distribution"

import { PrimaryButton } from "src/styled"
import { OptimizationContainer, BaselineContainer } from "./styled"

export default function BaselineSummary({ optimizer, constraintsFileName, baseline, selectedPreset, setSelectedPreset, selectedResultIndex, setSelectedResultIndex, goNext }) {

  const [currentTasks, setCurrentTasks] = useState(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
  const baselineTasks = Object.values(optimizer?.plannedproject?.tasks)

  const [baselineResults, setBaselineResults] = useState(optimizer?.plannedproject)
  const [steps, setSteps] = useState(optimizer?.project?.settings?.optimizationSteps)

  const [data, setData] = useState(optimizer?.results)
  const [listOfPresets, setListOfPresets] = useState(Object.keys(data))
  const [selectedPresetResult, setSelectedPresetResult] = useState(data[selectedPreset])

  const selectedPresetResultOutput = data[selectedPreset]?.at(selectedResultIndex)?.output
  const newProjectDuration = selectedPresetResultOutput ? selectedPresetResultOutput?.projectDurationDays : 0

  const newTasks = Object.values(optimizer?.project?.tasks)
  const baselineCriticalTasks = optimizer?.planned?.criticalPath
  const newCriticalTasks = selectedPresetResultOutput?.criticalPath

  const baselineResources = Object.values(optimizer?.plannedproject?.resources)

  useEffect(() => {

    if (optimizer?.results["Balanced"] === undefined) {
      setTimeout(() => {
        optimizer.optimizeDefaults()
        setSelectedResultIndex(selectedPresetResult?.length - 1)
        setBaselineResults(optimizer?.plannedproject)
        setSteps(optimizer?.project?.settings?.optimizationSteps)
      }, 10)
      setTimeout(() => {
        setSelectedPresetResult(optimizer?.results[selectedPreset])
        setSelectedResultIndex(optimizer?.results[selectedPreset].length - 1)
      }, 1000)
    }
    else return null
  }, [])

  useEffect(() => {
    const renderUi = setInterval(() => {
      setData(optimizer?.results)
      setListOfPresets(Object.keys(optimizer?.results));
      setCurrentTasks(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
    }, 1000);

    return () => clearInterval(renderUi);
  }, [])

  useEffect(() => {
    setCurrentTasks(Object.values(optimizer?.project?.tasks).filter((i) => i.newFinish))
  }, [selectedPreset])

  useEffect(() => {
    if (optimizer && selectedPresetResult?.length - 1 === selectedResultIndex) {
      optimizer?.simulate(selectedPresetResult?.at(selectedResultIndex).durationRatios)
    }
  }, [selectedPresetResult])

  useEffect(() => {
    Object.values(optimizer?.plannedproject?.tasks).filter((d) => d.newFinish).map((i) => {
      // console.llg(new Date().toISOString().slice(0, 10))
      // console.log(i.newFinish?.data)
      // console.log(new Date(i.plannedFinish).toISOString().slice(0, 10))
      // console.log(new Date(i.newFinish?.data).toISOString().slice(0, 10))
      // console.log(new Date().getTime() > i.plannedFinish)
    })
  }, [])


  const calculatePercentage = (optimized, baseline) => {
    const diff = optimized && baseline ? (optimized - baseline) : 0
    const percentage = ((diff / baseline) * 100)
    const checkPositive = Math.sign(percentage)
    if (checkPositive === 1) {
      return <span className='badge minus'>{(Math.round(percentage))}%<ArrowUpwardIcon /></span>
    }
    else if (checkPositive === -1) {
      return <span className='badge plus'>{Math.abs((Math.round(percentage * 100) / 100))}%<ArrowDownwardIcon /></span>
    }
    else { return <span className='badge'>{(Math.round(percentage))}%</span> }
  }

  const renderSwitch = (label) => {
    switch (label) {
      case "Balanced":
        return `Best combination of the others`
      case "Fastest":
        return `Shortest project duration`
      case "Minimum Resources":
        return `Least amount of required resources`
      case "Levelled Resources":
        return `Best resource distribution`
      case "Constraint Compliance":
        return `Satisfies all constraints`
      // return `Satisfies all constraints ${data[label].length}`
      default:
        return ""
    }
  }

  const buttonClick = () => {
    setSelectedResultIndex(optimizer?.results[selectedPreset].length - 1)
    goNext()
  }

  return <>
    <OptimizationContainer>
      <h2>
        Baseline Summary
        <div className="chip primary"><small>{optimizer?.project?.settings?.optimizationSteps} steps</small></div>
        <div className="chip primary"><small>{optimizer?.project?.settings?.minDurationRatio * 100}% - {optimizer?.project?.settings?.maxDurationRatio * 100}% ratio</small></div>
      </h2>

      <BaselineContainer>
        <div className="chart">
          <h4>Comparison Chart</h4>
          <div className="legend">
            <div className="item">
              <div style={{ width: "14px", height: "14px", background: "#cccccc", marginRight: "5px", borderRadius: "50%" }}></div>
              <div>Baseline</div>
            </div>

            {listOfPresets.map((p) => {
              return <div key={p} className={`item ${constraintsFileName === "No file chosen" && p === "Constraint Compliance" ? "hidden" : ""}`}>
                <div style={{ width: "14px", height: "14px", background: plot_color_palette.ComparisonPlot[p], marginRight: "5px", borderRadius: "50%" }}></div>
                <div>{p}</div>
              </div>
            })}
          </div>

          <DistributionPlot
            key={selectedPresetResult}
            constraintsFileName={constraintsFileName}
            results={data || []}
            baselineResults={baselineResults || []}
            width="750"
            steps={steps}
            selectedPreset={selectedPreset}
            selectedResultIndex={selectedResultIndex}
            x_label="Duration (days)"
            y_label="Maximum Resource (units)"
            onClick={(preset, i) => {
              setSelectedPreset(preset)
              setSelectedResultIndex(i)
            }}
          />
        </div>

        <div className="sidebar">
          <div className="metrics">
            <div className="stats">
              <div className="title">Project Duration {calculatePercentage(newProjectDuration, Math.round(baseline?.duration))}</div>
              <div className="figure">
                <h2>
                  {Math.round(newProjectDuration)}&nbsp;
                  <span className="subtitle">{Math.round(baseline?.duration)}</span>&nbsp;days
                </h2>
              </div>
            </div>
            <div className="stats">
              <div className="title">Changed Tasks vs Total N of Tasks&nbsp;
                {/* <span className='badge'>
                  {Math.abs(Math.round((100 - (((baselineTasks?.length - currentTasks?.length) / baselineTasks?.length) * 100)) * 100) / 100)}%</span> */}
              </div>
              <div className="figure">
                <h2>{currentTasks?.length}/{Math.round(baselineTasks?.length)}</h2>
              </div>
            </div>
            <div className="stats">
              <div className="title">Tasks on Critical Path {calculatePercentage(newCriticalTasks?.length, baselineCriticalTasks?.length)}</div>
              <div className="figure">
                <h2>{newCriticalTasks?.length}&nbsp;<span className="subtitle">{baselineCriticalTasks?.length}</span></h2>
              </div>
            </div>
            <div className="stats">
              <div className="title">Total Resources</div>
              <div className="figure">
                <h2>{baselineResources?.length}</h2>
              </div>
            </div>
          </div>

          <div className="options">
            <h4>Optimization Presets</h4>
            <p>These available presets are pre-configured settings that are designed to deliver the best performance or quality for each individual specific use case.</p>
            <FormControl component="fieldset">
              <RadioGroup
                value={selectedPreset}
                onChange={(e) => {
                  setSelectedPreset(e.target.value)
                  setSelectedResultIndex(optimizer?.results[e.target.value].length - 1)
                }}
              >
                {listOfPresets.map((o) => {
                  return <div key={o} className={`presets${constraintsFileName === "No file chosen" && o === "Constraint Compliance" ? " hidden" : ""}`}>
                    <FormControlLabel disabled={data[o].length < 10} value={o} control={<Radio />} label={o} />
                    <div style={{ fontSize: "12px", fontFamily: "Inter" }}>
                      {renderSwitch(o)}
                    </div>
                  </div>
                })}
              </RadioGroup>
            </FormControl>
          </div>
          <PrimaryButton variant="contained" onClick={buttonClick}>
            Next
          </PrimaryButton>
        </div>
      </BaselineContainer>
    </OptimizationContainer>
  </>
}