import { addToFilename, exportFile, parser, toObject } from "./utils";
import { Project, Task, Resource, Calendar, TaskPredecessor, TaskResource } from "./models";
import * as samples from "./samples";

/*
https://docs.oracle.com/cd/F25600_01/English/Mapping_and_Schema/ms_project_import_export_data_map_xml/helpmain.htm?toc.htm?97947.htm
https://docs.oracle.com/cd/F51303_01/English/Mapping_and_Schema/xer_import_export_data_map_project/
https://learn.microsoft.com/en-us/office-project/xml-data-interchange/project-elements-and-xml-structure?view=project-client-2016
*/

export class P6XerReader {
  async read(file = new File()) {
    let reader = new FileReader();
    return new Promise((resolve) => {
      reader.onload = (e) => {
        this.rawText = e.target.result.trim();
        this.rawData = this.rawTextToRawData(this.rawText);
        this.project = this.rawDataToProject(this.rawData);
        this.project.projectFileName = file.name;
        resolve(this.project);
      };
      reader.readAsText(file);
    });
  }

  rawTextToRawData(text = "") {
    let header,
      tables = {},
      table = [],
      keys;
    text.split("\n").forEach((line) => {
      let columns = line.split("\t").map((x) => x.trim());
      let type = columns[0],
        items = columns.slice(1);
      switch (type) {
        case "ERMHDR":
          header = items;
          break;
        case "%T":
          let name = items[0];
          table = tables[name] = [];
          break;
        case "%F":
          keys = items;
          break;
        case "%R":
          let values = items;
          table.push(toObject(keys, values));
          break;
        case "%E":
          break;
        default:
          break;
      }
    });
    let rawData = { header, tables };
    return rawData;
  }

  rawDataToProject(rawData) {
    let { tables } = rawData;
    let project = new Project();

    tables.TASK.forEach((x = samples.p6Task) => {
      let y = new Task();
      y.id = x.task_id;
      y.name = x.task_name;
      y.code = x.task_code;
      y.type = x.task_type.slice(3);

      y.calendarId = x.clndr_id;
      y.predecessors = {};
      y.resources = {};

      y.plannedStart = parser.parseDate(x.target_start_date);
      y.plannedFinish = parser.parseDate(x.target_end_date);
      y.plannedDuration = parseInt(x.target_drtn_hr_cnt);

      y.actualStart = parser.parseDate(x.act_start_date);
      y.actualFinish = parser.parseDate(x.act_end_date);

      project.tasks[y.id] = y;
    });

    Object.values(tables.RSRC)?.forEach((x = samples.p6Resource) => {
      let y = new Resource();
      y.id = x.rsrc_id;
      y.name = x.rsrc_name;
      y.code = x.rsrc_short_name;
      y.type = { Labor: "Labor", Equip: "Equipment", Mat: "Material" }[x.rsrc_type.slice(3)];

      project.resources[y.id] = y;
    });

    tables.CALENDAR.forEach((x = samples.p6Calendar) => {
      let y = new Calendar();
      y.id = x.clndr_id;
      y.name = x.clndr_name;
      y.weekHourCount = parseInt(x.week_hr_cnt);

      project.calendars[y.id] = y;
    });

    tables.TASKPRED.forEach((x = samples.p6TaskPredecessor) => {
      let y = new TaskPredecessor();
      y.id = x.task_pred_id;
      y.taskId = x.task_id;
      y.predecessorId = x.pred_task_id;

      y.type = x.pred_type.slice(3);
      y.lagHour = parseInt(x.lag_hr_cnt);

      let task = project.tasks[y.taskId];
      task.predecessors[y.predecessorId] = y;
    });

    Object.values(tables.TASKRSRC)?.forEach((x = samples.p6TaskResource) => {
      let y = new TaskResource();
      y.id = x.taskrsrc_id;
      y.taskId = x.task_id;
      y.resourceId = x.rsrc_id;

      y.plannedUnits = parseFloat(x.target_qty);
      let task = project.tasks[y.taskId];
      y.plannedUnitsPerHour = task.plannedDuration > 0 ? y.plannedUnits / task.plannedDuration : 0;

      task.resources[y.resourceId] = y;
    });

    return project;
  }

  projectToRawData(project) {
    let rawData = JSON.parse(JSON.stringify(this.rawData));
    let { tables } = rawData;

    tables.TASK.forEach((x = samples.p6Task) => {
      let y = project.tasks[x.task_id];
      if (y?.optimizable) {
        x.target_start_date = parser.strDate(y.newStart.data);
        x.target_end_date = parser.strDate(y.newFinish.data);

        /*
        original      13    target_drtn_hr_cnt
        actual        0     act_drtn_hr_cnt
        remaining     24    remain_drtn_hr_cnt
        at complete   24    total_drtn_hr_cnt
        total float   0     total_float_hr_cnt
        free float    0     free_float_hr_cnt 
        */
        x.target_drtn_hr_cnt = x.remain_drtn_hr_cnt = x.total_drtn_hr_cnt = y.newDuration.data;

        if (x.act_start_date) x.act_start_date = parser.strDate(y.newStart.data);
        if (x.act_end_date) x.act_end_date = parser.strDate(y.newFinish.data);
      }
    });

    tables.TASKRSRC?.forEach((x = samples.p6TaskResource) => {
      let y = project.tasks[x.task_id];
      if (y?.optimizable) {
        y = y.resources[x.rsrc_id];
        if (y) {
          x.target_qty_per_hr = y.newUnitsPerHour?.data;
        }
      }
    });

    return rawData;
  }

  rawDataToRawText(rawData) {
    let { header, tables } = rawData;
    let lines = [];
    lines.push(["ERMHDR", ...header].join("\t"));
    Object.entries(tables).forEach(([name, table]) => {
      lines.push(["%T", name].join("\t"));
      lines.push(["%F", ...Object.keys(table[0])].join("\t"));
      table.forEach((row) => {
        lines.push(["%R", ...Object.values(row)].join("\t"));
      });
    });
    lines.push("%E");
    return lines.join("\n");
  }

  exportToOriginal(project) {
    let rawData = this.projectToRawData(project);
    let rawText = this.rawDataToRawText(rawData);
    let filename = addToFilename(project.projectFileName, "_FrontlineExport");
    exportFile(filename, rawText);
  }
}